@font-face {
  font-family: Bold;
  src: url(../assets/fonts/poppins/Poppins-Bold.ttf);
}
@font-face {
  font-family: SemiBold;
  src: url(../assets/fonts/poppins/Poppins-SemiBold.ttf);
}
@font-face {
  font-family: Regular;
  src: url(../assets/fonts/poppins/Poppins-Regular.ttf);
}
.animate.fade-in {
  opacity: 0;
  animation: 0.3s 0.3s fadeIn both;
}
.animate.no-delay {
  animation: 0.3s fadeIn both;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
body {
  padding: 0;
  margin: 0;
  font-family: Regular;
  font-size: 14px;
  color: #333333;
}

:root {
  --gutter: 20px;
}

.link,
a {
  color: #193f85;
  text-decoration: none;
}
.link.primary,
a.primary {
  color: #9e5ce1;
}
.link:active,
a:active {
  text-decoration: none;
}
.link:hover,
a:hover {
  text-decoration: underline;
}

.bold {
  font-family: SemiBold;
}

.cursor {
  cursor: pointer;
}

select {
  border: 1px solid #e5e5e5;
  border-radius: 5px;
  width: 100%;
  padding: 12px 10px;
}

.hide {
  display: none;
}

@media screen and (max-width: 800px) {
  .s-100 {
    width: 100% !important;
  }
}

.row {
  display: flex;
  flex-wrap: wrap;
}

.col-6 {
  width: 50%;
}

.col-12 {
  width: 100%;
}

app-card {
  width: 100%;
}

app-card[ng-reflect-size=lg] {
  width: calc(75% - var(--gutter));
}

app-card[ng-reflect-size=md] {
  width: calc(50% - var(--gutter));
}

app-card[ng-reflect-size=sm] {
  width: calc(25% - var(--gutter));
}

.plus-icon {
  position: relative;
  width: 30px;
  height: 30px;
  border: 2px solid #9e5ce1;
  border-radius: 20px;
  transition: 0.3s ease all;
}
.plus-icon::after {
  position: relative;
  left: 6px;
  top: -4px;
  font-size: 27px;
  color: #9e5ce1;
  content: "+";
}
.plus-icon:hover {
  background-color: #9e5ce1;
  color: #fff;
}
.plus-icon:hover::after {
  color: #fff !important;
}

.line {
  border-bottom: 1px solid #e5e5e5;
  margin: 20px 0;
}

.txt-md {
  font-size: medium;
}

.txt-lg {
  font-size: large;
}

.txt-xlg {
  font-size: x-large;
}

.text-center {
  text-align: center;
}

.mt-20 {
  margin-top: 20px;
}

.common-reorder {
  position: relative;
  cursor: pointer;
}
.common-reorder::after {
  position: absolute;
  top: calc(50% - 5px);
  right: -20px;
  border-style: solid;
  border-width: 0 4px 4px 4px;
  border-color: #aaaaaa transparent;
  content: "";
}
.common-reorder::before {
  position: absolute;
  top: calc(50% + 5px);
  right: -20px;
  border-style: solid;
  border-width: 4px 4px 0 4px;
  border-color: #aaaaaa transparent;
  content: "";
}

/* in-flight clone */
.gu-mirror {
  position: fixed !important;
  margin: 0 !important;
  z-index: 9999 !important;
  opacity: 0.8;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=80)";
  filter: alpha(opacity=80);
  pointer-events: none;
}

/* high-performance display:none; helper */
.gu-hide {
  left: -9999px !important;
}

/* added to mirrorContainer (default = body) while dragging */
.gu-unselectable {
  -webkit-user-select: none !important;
  -moz-user-select: none !important;
  -ms-user-select: none !important;
  user-select: none !important;
}

/* added to the source element while its mirror is dragged */
.gu-transit {
  opacity: 0.2;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=20)";
  filter: alpha(opacity=20);
}